.modal_wrap{width: 100%; height: 100%; position: fixed; top: 0; left: 0; z-index: 99;}
.modal_wrap.off{display: none;}
.modal_bg{width: 100%; height: 100%; background: rgba(0,0,0,0.4);}

.modal_con{width: 28%; background-color: #fff; height: 700px; position: absolute; top: 10%; left: 50%; transform: translateX(-50%); border-radius: 14px;  box-sizing: border-box;}
.modal_con .info_tit{width: 100%; color:#99a1a8; height: 66px; line-height: 66px; border-bottom: 1px solid #d9d9d9; padding:0 40px; box-sizing: border-box;font-size: 20px; letter-spacing: -0.08rem; font-weight: 700;}
.modal_con .close_btn{font-size: 30px; position: absolute; top:10px; right: 40px; color:#99a1a8; cursor: pointer;}

.todaydelv_info{padding:20px 40px; text-align: center;}
.todaydelv_info > p{font-size: 16px; margin: 30px 0 90px;}
.todaydelv_info .pick_info {margin-bottom: 50px;}
.todaydelv_info .pick_info p{font-size: 20px; font-weight: 700;}
.todaydelv_info .pick_info img{width: 10%; vertical-align: middle;}