.recommend_wrap{width: 100%;}
.recommend_wrap .move_wrap{position: relative; display: flex; justify-content: space-between; align-items: center;}
.recommend_wrap .move_wrap .rcmd_prd{width: 100%; display: flex; gap: 10px; overflow-y: hidden; overflow-x: scroll; scroll-behavior: smooth; transition: all 0.8s ease-in-out; padding: 40px 0;}
.recommend_wrap .left_arrow,
.recommend_wrap .right_arrow{display: inline-block; cursor: pointer; font-size: 20px; background-color: rgba(0,0,0,0.3); width: 36px; height: 36px; line-height: 34px; text-align: center; font-weight: 700; color: #fff; border-radius: 100%; position: absolute; top: 50%; transform: translateY(-50%);}
.recommend_wrap .left_arrow{left:-26px;}
.recommend_wrap .right_arrow{right:-26px;}
.recommend_wrap .rcmd_prd{display: inline-block;}
.recommend_wrap .rcmd_prd a{display: flex; flex: 0 0 330px;}
.recommend_wrap .rcmd_prd a img{width: 40%; height: auto;}
.recommend_wrap .rcmd_prd a .text_wrap{padding-left: 10px;}
.recommend_wrap .rcmd_prd a span{font-weight: 700; font-size: 14px;}
.recommend_wrap .rcmd_prd a .prd_price{display: block; color: #e02020;margin: 5px 0;}
.recommend_wrap .rcmd_prd a .prd_addTag{background-color: #f374b7; color: #fff; border-radius: 14px; padding: 2px 6px; font-size: 12px;}
.recommend_wrap .rcmd_prd a .prd_addTag.sale{background-color: #f65c60;}

.hide-scrollbar::-webkit-scrollbar{display: none;}