.header{width: 100%; border-bottom: 1px solid #ddd;}
.header_wrap{width: 1020px; margin: 20px auto; display: flex; align-items: center; justify-content: space-between;}
.header_wrap > a{width: 246px; height: 40px;}
.header_wrap a img{width: 100%;}

.search_wrap{position: relative; width: 400px;}
.search_wrap input{width: 340px; height:38px; border-radius: 20px; border:1px solid #82dc28; padding:10px; box-sizing: border-box;}
.search_wrap .shearch_btn{position: absolute; top: 8px; right: 70px; background-color: transparent; border: none; cursor: pointer; text-indent: -999rem; font-size: 0; background: url(data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.44339 14.378C11.7226 14.3764 14.3796 11.7168 14.3779 8.43756C14.3763 5.15837 11.7167 2.50138 8.4375 2.50301C5.15831 2.50463 2.50132 5.16426 2.50294 8.44345C2.50457 11.7226 5.1642 14.3796 8.44339 14.378Z' stroke='%23131518' stroke-width='1.5'/%3E%3Cpath d='M16.9623 18.0436C17.2552 18.3365 17.7301 18.3365 18.023 18.0436C18.3159 17.7507 18.3159 17.2758 18.023 16.9829L16.9623 18.0436ZM11.9623 13.0436L16.9623 18.0436L18.023 16.9829L13.023 11.9829L11.9623 13.0436Z' fill='%23131518'/%3E%3C/svg%3E); width:20px; height:20px;}
.menu_wrap{width: 300px;}
.menu_wrap ul{display: flex; justify-content: space-between;}
.menu_wrap li:first-child a{background: url('https://static.oliveyoung.co.kr/pc-static-root/image/comm/ico_delivery_flag.png') no-repeat; background-size: 24%; background-position: 86% 0;}
.menu_wrap li{position: relative; color: #000; font-size: 14px; width: 86px;}
.menu_wrap li a{width: 100%; display: inline-block;}
.menu_wrap li:after{content: ""; position: absolute; top: 3px; right: -9px; width: 1px; height: 16px; background-color: #d6d6d6;}
.menu_wrap li:last-child::after{display: none;}