.product-container{width: 1020px; padding:10px; box-sizing: border-box;  margin:40px auto; display: flex; justify-content: space-around;}
.left_area{width: 510px;}
.left_area > img{width: 510px; height: 510px;}
.left_area ul{width: 90%; display: flex; margin:10px auto;}
.left_area ul li{cursor: pointer; width: calc(100%/5); margin-right: 5px;}
.left_area ul li:last-child{margin-right: 0;}

.right_area{padding:0 0 0 40px; width: 450px;}
.right_area .product_info{margin-bottom: 14px;}
.right_area .product_more_info{margin-bottom: 26px;}
.right_area .product-selection{margin-bottom: 14px;}
.right_area .product_info span{ width: 50px; background-color: #6fcff7; font-size: 12px; color: #fff; padding:5px 5px; border-radius: 20px; display: inline-block; text-align: center;}
.right_area .product_more_info h3{font-size: 16px; font-weight: 700; margin-bottom: 10px;}
.right_area .product_more_info p{color: #555; font-weight: 700; display: flex; justify-content: start; width: 100%; font-size: 14px; margin-bottom:6px;}
.right_area .product_more_info p span{width: 20%;}
.right_area .product_more_info div{width: 80%; font-weight: 400;}
.right_area .title{font-size: 26px; font-weight: 400; margin-bottom:6px;}
.right_area .price{font-size: 26px; font-weight: 400; color: #e02020; font-weight: 700; margin-bottom: 6px;}

.product-selection{position: relative; width: 100%; box-sizing: border-box;}
.product-selection .option_select{width: 200px; border: 1px solid #ddd; font-size: 14px; font-weight: 700; display: block; width: 100%; padding:12px 3%; border-radius: 10px; box-sizing: border-box; margin-bottom: 8px; position: relative;}
.product-selection .option_select.on{border: 1px solid #9bce26;}
.product-selection .option_select:after{content: ''; width: 20px; height: 20px; background: url('https://cf-images.oliveyoung.co.kr/review/img/arrow_down.svg') center no-repeat; position: absolute; top: 26%; right: 3%;}
.product-selection .option_select.on:after{transform: scale(-1);}
.product-selection .option_wrap{position: relative;}
.product-selection .option_prd{display: none; border: 1px solid #9bce26; margin:5px 0 14px; border-radius: 4px; padding:10px; height: 200px; overflow-y: scroll; position: absolute; top: -8px; left: 0; background-color: #fff; z-index: 9;}
.product-selection .option_prd.on{display: block;}
.product-selection .option_prd li{height: 38px; line-height: 38px; color: #333; border-bottom: 1px solid #e9e9e9; padding: 10px 0;}
.product-selection .option_prd li a{width: 100%; height: 100%; overflow: hidden; display: block;}
.product-selection .option_prd li img{width: 10%; vertical-align: sub;}
.product-selection .option_prd li span{float: right; font-weight: 700; font-size: 14px;}
.product-selection .num_wrap{justify-content: space-between; align-items: center; height: 70px; line-height: 5\70px; background-color: #f9f9f9; border: 1px solid #e9e9e9; border-radius: 10px; margin: 10px 0;}
.product-selection .num_wrap{display:none; width: 100%; padding: 10px 20px; box-sizing: border-box;}
.product-selection .num_wrap.on{display:flex;}
.product-selection .num_wrap select{width: 80px;}
.product-selection .product_name{display: inline-block; font-size: 14px; font-weight: 700;}
.product-selection .num_select{width: 140px; border: 1px solid #e9e9e9; border-radius: 7px; height: 34px; line-height: 28px; overflow: hidden;}
.product-selection .num_select input{width: 50%; height: 100%; border:0; vertical-align: middle; box-sizing: border-box; text-align: center;}
.product-selection .num_select button{width: 25%; height: 100%; border:0; vertical-align: middle; cursor: pointer; font-weight: 700; color: #8d8d8d; font-size: 20px;}

.btnWRap{width: 100%; display: flex; justify-content: space-between;}
.selectBtn, .wishBtn{border: 1px solid #f27370; padding:18px 30px; font-weight: 700; color: #f27370; cursor: pointer; background-color: #fff; font-size: 16px; font-weight: 700;}
.buyBtn, .giftBtn{border: 1px solid #f27370; padding:18px 28px; font-weight: 700; color: #fff; cursor: pointer; background-color: #f27370; font-size: 16px; font-weight: 700;}
.btnWRap .giftBtn{padding:18px 6px; background: url('https://static.oliveyoung.co.kr/pc-static-root/image/comm/icon-gift-32-line-fff.png') 50% 50% no-repeat; text-indent: -999rem; font-size: 0; width: 76px; background-color: #f27370;}
.btnWRap .wishBtn{padding:18px 6px; background: url('https://static.oliveyoung.co.kr/pc-static-root/image/comm/icon-like-32-line.png') 50% 50% no-repeat; text-indent: -999rem; font-size: 0; width: 76px;}

.cart{width: 100%; border: 2px solid #f27370; padding:10px; box-sizing: border-box; border-radius:10px; margin-bottom: 30px;}
.cart .sub_tit{position: relative; font-weight: 700; font-size: 16px; margin-bottom:10px;  background-color: #f27370; width: 40px; padding: 4px 8px; border-radius: 10px; color: #fff;} 
.cart .sub_tit:after{content: ''; position: absolute; top: 4px; right: -26px; width: 26px; height: 26px; background: url('https://static.oliveyoung.co.kr/pc-static-root/image/comm/ico_delivery_flag.png') center no-repeat; background-size: 100%;}
.cart ul li{display: flex; align-items: center; justify-content: space-between; font-weight: 700; font-size: 14px; height: 26px; line-height: 26px;}
.cart .close_btn{position: relative; font-size: 0; text-indent: -999rem; width: 18px; height: 18px; background-color: #ddd; border-radius: 100%; display: inline-block; cursor: pointer;}
.cart .close_btn::before{content: ''; background-color: #fff; width: 1px; height: 14px; position: absolute; top: 2px; left: 9px; transform: rotate(45deg);}
.cart .close_btn::after{content: ''; background-color: #fff; width: 1px; height: 14px; position: absolute; top: 2px; left: 9px; transform: rotate(-45deg);}

.allPrice{width: 100%; color:#e02020; font-weight: 700; font-size: 18px; padding-bottom:18px; border-bottom:1px solid #e02020;}
.allPrice p{display: flex; justify-content: space-between;}

.input_box{margin-top: 20px;}
.input_box input[type="checkbox"]{margin-right: 10px; width: 16px; height: 16px; vertical-align: middle;}
.input_box label{margin-right: 10px;} 
.info_Btn{width: 20px; height: 20px; border-radius: 100%; background-color: #fff; border: 1px solid #a1a1a1; color: #a1a1a1; cursor: pointer; animation: blink 1s infinite;}


@keyframes blink {
   0%{color: #a1a1a1; border: 1px solid #a1a1a1;}
   50%{color: #e02020; border: 1px solid #e02020; box-shadow: 1px 2px 2px rgba(236,22,22,0.5);}
}
