.middle_wrap{width: 1020px; margin: 0 auto; position:relative;}
.middle_info{position:relative; width: 100%; background-color: #f7f7f7; padding: 20px 20px; box-sizing: border-box; border-radius: 4px; margin-bottom:20px;}
.middle_info_tit{font-weight: 700; font-size: 18px; color: #333; margin-bottom:10px;}
.more_info_btn{position: absolute; bottom: 20px; right: 14px; background-color: transparent; border: none; color: #bdbdbd; font-size: 16px; cursor: pointer;}

.canget_store{width: 100%; font-size: 20px; font-weight: 700; border: 1px solid #dadde0; border-radius: 4px; padding: 24px; margin-bottom:40px; box-sizing: border-box;}
.canget_store a{display: block; background: url('https://static.oliveyoung.co.kr/pc-static-root/image/product/icon_store_search_new.png') 0 70% no-repeat; background-size: 2.2%; padding-left: 30px;}
